<template>
    <div class="content-container">
        <Header>充值</Header>
        <div class="recharge-container">
            <div class="recharge-title">
                <span>充值方式</span>
                <div class="recharge-bank" @click="chooseBank()">
                    <svg class="icon wallet-icon" aria-hidden="true">
                        <use xlink:href="#icon-zhongguoyinhang" />
                    </svg>
                    <span>{{this.defaultBankCard}}</span>
                </div>
            </div>
            <div class="recharge-content">
                <span>充值金额</span>
                <div class="input-container">
                    <strong class="pay-icon">￥</strong>
                    <input v-model="money" type="text" class="mui-input-clear" placeholder="请输入金额">
                </div>
            </div>
            <button  id='promptBtn' type="button" class="mui-btn mui-btn-outlined l-btn">充值</button>
        </div>
    </div>
</template>

<style scoped>
.recharge-container{
    padding-top: 40px;
}
.recharge-title{
    padding-top: 20px;
    padding-left: 15px;
    background-color: whitesmoke;
    padding-bottom: 60px;
    display: flex;
}
.recharge-bank{
    margin-left: 15px;
}
.recharge-content{
    width: 100%;
    border-radius: 15px;
    padding: 25px;
}
.pay-icon{
    font-size: 26px;
}
.input-container{
    display: flex;
    align-items:center;
}
.mui-input-clear{
    bottom: 0;
    border: none
}
.mui-btn{
    display: block;
    background-color: #6e8b3d;
    color: white;
    margin: 0 auto;
    width: 80%;
    margin-top: 20px;
}
</style>

<script>
import Header from '../../components/LeftHeader'
import InputPassword from '../../components/InputPassword'
import {mapGetters} from 'vuex'
export default {
  components: {
    Header,
    InputPassword
  },
  data () {
    return {
      bankCardData: [],
      root: process.env.API_HOST,
      defaultBankCard: '',
      binkid: '',
      money: 0
    }
  },
  computed: {
    ...mapGetters(['curUserInfo'])
  },
  mounted () {
    this.init()
    this.initBankCard()
  },
  methods: {
    init () {
      var info = document.getElementById('info')
      document.getElementById('promptBtn').addEventListener('tap', (e) => {
        e.detail.gesture.preventDefault() // 修复iOS 8.x平台存在的bug，使用plus.nativeUI.prompt会造成输入法闪一下又没了
        var btnArray = ['取消', '确定']
        mui.prompt('请输入支付密码：', '', '充值到零钱', btnArray, (e) => {
          if (e.index == 1) {
            this.recharge()
          //   info.innerText = '谢谢你的评语：' + e.value
          } else {
          //   info.innerText = '你点了取消按钮'
          }
        })
        document.querySelector('.mui-popup-input input').type='password'
      })
    },
    chooseBank () {
      var picker = new mui.PopPicker()
      picker.setData(this.bankCardData)
      picker.show( (items) => {
        this.defaultBankCard = items[0].text
        this.bankid = items[0].bankid
        // 返回 false 可以阻止选择框的关闭
        // return false;
      })
    },
    initBankCard () {
      const {userid} = this.curUserInfo
      this.$ajax.post({
        url: this.root + 'bankcard/selectbankcard',
        data: {userid}
      })
        .then(res => {
          if (res.status) {
            this.bankCardData = res.result
            this.bankCardData.map(item => {
              item.value = item.bankcardnum.slice(item.bankcardnum.length - 5, item.bankcardnum.length - 1)
              item.text = item.bankcompany + '(' + item.value + ')'
              return item
            })
            this.defaultBankCard = this.bankCardData[0].text
            this.bankid = this.bankCardData[0].bankid
          }
        })
    },
    // 充值接口
    recharge () {
      const {userid} = this.curUserInfo
      const bankid = this.bankid
      const money = Number(this.money)
      const params = {
        userid,
        bankid,
        money
      }
      this.$ajax.post({
        url: this.root + 'bankcard/recharge',
        data: params
      })
        .then(res => {
          if (res.status) {
            this.$router.push('/wallet')
          } else {
            mui.toast(res.result)
          }
        })
    }
  }
}
</script>


